* {
  padding: 0;
  margin: 0;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 14px;
  line-height: 1.1;
}

.container{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.task-list {
  width: 500px;
}

#start-message {
  margin: 10px;
  text-align: center;
}

.task {
  position: relative;
  display: flex;
  align-items: center;
  border: 1px solid #ccc;
  padding: 0 80px 0 10px;
  margin: 15px 0;
  border-radius: 5px;
  min-height: 34px;
}

.task p {
  margin-left: 4px;
}

.completed {
  background-color: royalblue;
  color: white;
}

.completed p {
  text-decoration: line-through;
}

.input-panel {
  width: 500px;
  margin: 8px;
  padding: 2px;
  display: flex;
  gap: 15px;
}

.input-panel button {
  padding: 4px;
  width: 25%;
  background: #000;
  color: #fff;
  border-radius: 5px;
  height: 34px;
  cursor: pointer;
}

.input-panel button:hover{
  opacity: 0.8;
}

.input-panel input {
  height: 34px;
  border: 1px solid #ccc;
  padding: 0 10px;
  box-shadow: 1px 1px 5px #ccc;
  border-radius: 5px;
  display: flex;
  flex-grow: 1;
}
.delete{
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  width: 18px;
  height: 18px;
  background: url(../../assets/images/delete.svg);
  background-size: cover;
  cursor: pointer;
}